nav {
    margin: 25px 0;
    transition: 0.2s;
}

nav.sticky {
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 1000;
}

.nav-items {
    display: flex;
    justify-content: space-evenly
}

li.nav-item {
    font-family: 'Bebas Neue', cursive;
    font-size: 32px;
}